<template>
  <div class="vuelab-wrapper">
    <div @click="handleFullScreen">全屏</div>
    <!-- <div class="input-wrapper" @click="handleFullScreen('xiaonanzhi')" ref="xiaonanzhi"> -->
    <div class="input-wrapper" ref="xiaonanzhi">
      <input type="text"
        v-model="rateValue"
        v-focus="{'name': 'xiao', 'address': 'shenzhen'}"
        placeholder="请输入信息"
      >
    </div>
    <p v-if="rateValue">您输入的值是：{{rateValue | toUpper}}G</p>
    <div @click="toggleDialog">show dialog — {{ dialogVision }}</div>
    <RequestAnimationFrame />
    <DialogComp :show.sync="dialogVision" />
    <router-link to="/dynamic">动态组件集</router-link>
    <p><router-link to="/render">Render函数</router-link></p>
    <!-- <router-view></router-view> -->
  </div>
</template>

<script>
import DialogComp from "@/components/vuecomp/dialog";
import RequestAnimationFrame from '@/components/vuecomp/requestAnimationFrame';
import DemoMixin from '@/mixins/demo'

export default {
  components: {
    DialogComp,
    RequestAnimationFrame
  },
  mixins: [DemoMixin],
  watch: {
    dialogVision(newVal, oldVal) {
      // console.log("你边了", `新值:${newVal}`, oldVal);
    }
  },
  data() {
    return {
      dialogVision: false,
      rateValue: null,
      fullScreenStatus: true
    };
  },
  methods: {
    toggleDialog() {
      this.dialogVision = !this.dialogVision;
    },
    /**
     * 检测当前是否是全屏状态
     */
    isFullScreen() {
      return !!(
        document.fullscreen || 
        document.mozFullScreen ||                         
        document.webkitIsFullScreen ||       
        document.webkitFullScreen || 
        document.msFullScreen 
      );
    },
    // 全屏事件
    handleFullScreen(id){
      this.fullScreenStatus = this.isFullScreen()
      // let element = id && document.getElementById('id') || document.documentElement;
      let element = id && this.$refs[id] || document.documentElement;
      // 如果是全屏，退出
      console.log('isFullScreen', this.isFullScreen())
      if (this.isFullScreen()) {
        // if (document.exitFullscreen) {
        //   document.exitFullscreen();
        // } else if (document.webkitCancelFullScreen) {
        //   document.webkitCancelFullScreen();
        // } else if (document.mozCancelFullScreen) {
        //   document.mozCancelFullScreen();
        // } else if (document.msExitFullscreen) {
        //   document.msExitFullscreen();
        // }
        // console.log('已还原！');
      } else {    // 否则，进入全屏
        if (element.requestFullscreen) {
          element.requestFullscreen();
        } else if (element.webkitRequestFullScreen) {
          element.webkitRequestFullScreen();
        } else if (element.mozRequestFullScreen) {
          element.mozRequestFullScreen();
        } else if (element.msRequestFullscreen) {
          // IE11
          element.msRequestFullscreen();
        }
        console.log('已全屏！');
      }
    },
    miao() {
      console.log('lab的喵方法')
    }
  },
  mounted() {
    console.log(this.nanzhi)
    this.miao()
    // console.log(this.$options.components)
  },
  created() {
    console.log('666-Vuelab自己的created')
  }
};
</script>

